.movie-preview-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.movie-selected img {
    width: 200px;
}

.movie-preview, .movie-selected {
    width: 250px;
    background-color: aliceblue;
    border-radius: 12px;
    border: solid 2px cornflowerblue;
    padding: 8px 4px;
    margin: 4px;
    text-align: center;
}

.movie-selected {
    border: solid 2px rgb(0, 50, 143);
    background-color: aliceblue;
}

button {
    margin: 3px;
}

.centerX {
    position: relative;
    left: 50%;
    transform: translateX(-50%);
}